.date_pick_container {
  border: 1px solid;
  padding: 5%;
  /* margin: 200px 20px; */
  color: lightgrey;
  border-radius: 5px;
  width: 75%;

  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: 50%;
}
label {
  color: black;
}
.date_pick_button {
  top: 45%;
  position: relative;
}
.sort_button {
  margin: 1% 0% 1% 1%;
}
.sort_div {
  -ms-flex-direction: row;
      flex-direction: row;
  display: -ms-flexbox;
  display: flex;
  margin-left: auto;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.nights_div {
  margin-top: 2%;
  -ms-flex: 1 1;
      flex: 1 1;
}
.main {
  margin-top: 5%;
  
}
.cont{
  border: 1px solid lightgray;
  padding: 5%;
  color: black;
  border-radius: 5px;
}
.search_input {
  border-radius: 50px 0 0px 50px;
}
.search_button {
  border-radius: 0px 50px 50px 0;
}
.search_div {
  margin-top: 21%;
}
.price_search_div {
  margin-top: 10%;
}
.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 5px;
  border-radius: 5px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  -webkit-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #17a2b8;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #17a2b8;
  cursor: pointer;
}
.error {
  margin-top: 3%;
}
.btn-link {
  color: #17a2b8;
}
.card-container{
display: -ms-flexbox;
display: flex;
-ms-flex-wrap: wrap;
    flex-wrap: wrap;
}
.card{
 -ms-flex-preferred-size: 48%;
     flex-basis: 48%;
 margin: 1%;
}
@media (max-width: 800px) {
  .card{
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
  
   }
}


